body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: #f9f9f9;
}

.app-container {
  padding: 20px;
}

.app-header {
  font-size: 1.5rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 1rem;
}

.msr-title {
  color: #d32f2f;
}

.search-bar {
  display: block;
  margin: 0 auto 20px auto;
  padding: 10px;
  font-size: 16px;
  width: 80%;
  max-width: 500px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.table-container {
  overflow-x: auto;
}

.leaderboard-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.05);
}

.leaderboard-table th,
.leaderboard-table td {
  padding: 10px;
  text-align: center;
  white-space: nowrap;
  font-size: 14px;
  border-bottom: 1px solid #eee;
}

.leaderboard-table th {
  background-color: #f1f1f1;
  cursor: pointer;
  font-weight: bold;
}

.leaderboard-table tr:hover {
  background-color: #f9f9f9;
}

.highlight-msr {
  background-color: #0066cc;
  color: white !important;
  font-weight: bold;
  border-radius: 5px;
}













